@import url("caswow.library/css/caswow.css");
.icon-left,
.icon-right {
  display: inline-flex;
  align-items: center;
  gap: .375rem;
}
.icon-left svg,
.icon-right svg {
  flex-shrink: 0;
  width: 1em;
  height: 1em;
  fill: currentColor;
  transition: .2s ease-in-out transform;
}
